@charset "utf-8";
@font-face {
  font-family: main1;
  src: url(../static/font/BlackHanSans-Regular.woff) format(woff);
}
.wrap {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
  margin-top: 150px;
}
.mainImg {
  width: 500px;
}
.title {
  font-family: main1;
  font-size: 50px;
  color: #2945ff;
  margin: 50px 0;
}
.header {
  display: flex;
  justify-content: center;
  margin-left: -350px;
}
.answerBox {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  box-sizing: content-box;
  height: 30px;
}
.inputWrap {
  width: 700px;
  margin: 0 auto;
  display: flex;
  justify-content: right;
  margin-top: -50px;
}
.input {
  width: 300px;
  border-radius: 10px;
  border: 1px solid #ccc;
  margin-right: 5px;
  padding: 5px;
  text-align: center;
}
.bTn {
  background-color: #2945ff;
  color: #fff;
  border-radius: 10px;
  padding: 3px;
  border: none;
}
